.login-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: black;
}

.login-box {
  background-color: #1a1a1a;
  padding: 30px;
  border-radius: 10px;
  box-shadow: 0 2px 10px rgba(255, 255, 255, 0.1);
  text-align: center;
  max-width: 400px;
  width: 100%;
}

.login-box h2 {
  color: white;
}

.input-group {
  margin-bottom: 20px;
  text-align: left;
}

.input-group label {
  display: block;
  margin-bottom: 5px;
  font-weight: bold;
  color: white;
}

.input-group input {
  width: 100%;
  padding: 10px;
  border: 1px solid #555;
  background-color: #333;
  color: white;
  border-radius: 5px;
}

.login-btn, .skip-btn {
  width: 100%;
  background-color: #007bff;
  color: white;
  border: none;
  padding: 10px;
  border-radius: 5px;
  cursor: pointer;
  margin-top: 10px;
}

.login-btn:hover, .skip-btn:hover {
  background-color: #0056b3;
}

.signup-btn {
  background: none;
  color: #007bff;
  border: none;
  cursor: pointer;
  text-decoration: underline;
}

.google-login {
  margin-top: 20px;
  display: flex;
  justify-content: center;
}

.google-login button {
  background-color: #4285F4; /* Standard Google button color */
  color: white;
  padding: 10px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  width: 100%;
}

.google-login button:hover {
  background-color: #357ae8; /* Slightly darker Google blue on hover */
}

@media (max-width: 400px) {
  .login-box {
      padding: 20px;
  }

  .input-group input,
  .login-btn,
  .skip-btn,
  .google-login button {
      padding: 8px;
  }
}
